.button-arrow {
  min-width: 152px;
  height: 42px;
  text-align: center;
  color: #000;
  background: #f8d24e;
  border: none;
  border-radius: 10px;
  padding: 12px 52px 12px 34px;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  transition-property: background;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.button-arrow:after {
  content: "";
  width: 12px;
  height: 12px;
  background-image: url("arrow-black.4e10dc16.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-34px, 16px);
}

.button-arrow:hover {
  background: #0a8817;
}

.button-arrow:focus {
  font-size: 15px;
  font-weight: 700;
  line-height: 1.2;
}

.button-arrow-border {
  min-width: 196px;
  height: 42px;
  text-align: center;
  color: #fff;
  background: #aec6a5;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 12px 64px 12px 46px;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  transition-property: border;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.button-arrow-border:after {
  content: "";
  width: 12px;
  height: 12px;
  background-image: url("arrow-white-new.98496673.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-46px, 16px);
}

.button-arrow-border:hover {
  border: 2px solid #0a8817;
}

.button-arrow-border:focus {
  font-size: 15px;
  font-weight: 700;
  line-height: 1.2;
}

